import axios from '../plugins/axios.js';

export function getTableData() {
  return axios.get('/applyGuide/SBZNManage', null, 'teacher-admin');
}

/**
 * -
 */
export function getTableData2(queryParams) {
  return axios.get(
    '/awardInfo/nowAwardInfos',
    {
      params: queryParams
    },
    'teacher-admin'
  );
}
