<template>
  <div class="test-table">
    <c-filter-check-panel
      ref="checkPanel"
      :data="headers1"
      :successMsg="successMsg"
      :ajaxFun="ajaxTest"
    ></c-filter-check-panel>
    <el-button size="small" type="primary" @click="onPop"></el-button>
    <el-button size="small" type="primary" @click="onPop2">2</el-button>
    <h1>+</h1>
    <c-table
      ref="table"
      :headers="headers1"
      :data="tableData1"
      :border="border"
      :maxHeight="maxHeight"
      @cell-click="onCellClick"
      @header-click="onHeaderClick"
      @cell-dlclick="onCellDblClick"
      @row-click="onRowClick"
      @row-dblclick="onRowDblClick"
      @current-change="onCurrentChange"
    >
    </c-table>
    <h1></h1>
    <b-table
      :headers="headers2"
      :ajaxGetData="getTableData2"
      :spanMethod="spanMethod"
      :loading="true"
      @header-click="onHeaderClick"
    >
      <div
        class="test"
        slot-scope="scope"
        slot="column|t1"
        @click.stop="onTest(scope.row, scope.$index)"
      >
        <div>custom1-{{ scope.row.name }}</div>
        <div>custom2-{{ scope.row.date | formatDateTime('yyyy-MM-dd') }}</div>
      </div>
      <div
        class="test2"
        slot-scope="scope"
        slot="column|demo"
        @click.stop="onTest(scope.row, scope.$index)"
      >
        <div>custom1-{{ scope.row.name + 'eees' }}</div>
      </div>
    </b-table>
    <!-- <HlTable
      ref="table"
      :headers="headers2"
      :data="tableData2"
      :border="border"
      :showPagination="false"
      :maxHeight="maxHeight"
    >
      <div
        class="test"
        slot-scope="scope"
        slot="column|t1"
        @click.stop="onTest(scope.row, scope.$index)"
      >
        <div>custom1-{{ scope.row.name }}</div>
        <div>custom2-{{ scope.row.date | formatDateTime('yyyy-MM-dd') }}</div>
      </div>
      <div
        class="test2"
        slot-scope="scope"
        slot="column|t2"
        @click.stop="onTest(scope.row, scope.$index)"
      >
        <div>custom1-{{ scope.row.name + 'eees' }}</div>
      </div>
    </HlTable> -->
  </div>
</template>

<script>
import CTable from '@/components/common/CTable';
import BTable from '@/components/business/BTable';
import CFilterCheckPanel from '@/components/common/CFilterCheckPanel/index.js';
import CFilterCheckPanel2 from '@/components/common/CFilterCheckPanel/index.vue';
import UTILS from '@/plugins/common/utils';
import { Message, Button } from 'element-ui';
import { getTableData, getTableData2 } from '../../api/test';
export default {
  name: 'Home',
  components: {
    ElButton: Button,
    BTable,
    CTable,
    [CFilterCheckPanel2.name]: CFilterCheckPanel2
    // CFilterCheckPanel
  },
  data() {
    return {
      successMsg: '',
      checkList: [
        {
          label: '',
          prop: 'sage',
          show: true
          // required: true
        },
        {
          label: 'xxx',
          prop: 'nam',
          required: true
        }
      ],
      border: true,
      maxHeight: 300,
      headers1: [
        {
          label: '',
          type: 'index',
          prop: 'index',
          //typeindexindexfunction
          index: index => {
            return '' + (index + 1) + '';
          },
          width: 100,
          fixed: 'left'
        },
        {
          label: '',
          prop: 'name',
          width: 200,
          required: true
        },
        {
          label: 'waaa',
          width: 200
        },
        {
          label: '2',
          prop: 'name2',
          width: 200,
          hidden: true
        },
        {
          label: '',
          prop: 'date',
          formatter: (row, col, value) => {
            return UTILS.dateConverter('yyyy-MM-dd hh:mm:ss', value);
          }
        }
      ],
      headers2: [
        {
          label: '',
          type: 'index',
          //typeindexindexfunction
          index: (index, page, size) => {
            console.log(index, page);
            return '' + (index + 1 + (page - 1) * size) + '';
          },
          width: 100,
          fixed: 'left'
        },
        {
          label: '',
          sortable: 'custom',
          prop: 'schoolName',
          filters: [
            { text: '', value: '' },
            { text: '', value: '' }
          ],
          filterMathod: (value, row) => {
            return row.schoolName.indexOf(value) != -1;
          },
          width: 200
        },
        {
          label: 'slot + colType=column',
          colType: 'column',
          prop: 't1',
          width: 300
        },
        {
          label: 'slot + colType=column',
          colType: 'column',
          slotName: 'demo',
          prop: 't2',
          width: 300
        },
        {
          label: '(colType=btns)',
          colType: 'btns',
          prop: 'name',
          btns: [
            {
              label: '',
              type: 'primary',
              click: (row, rowIndex, btnIndex) => {
                console.log(row, rowIndex, btnIndex, this.tableData1);
                Message({
                  type: 'success',
                  message: `${rowIndex}${btnIndex}`
                });
              }
            },
            {
              label: '',
              size: 'small',
              type: 'success',
              click: (row, rowIndex, btnIndex) => {
                console.log(row, rowIndex, btnIndex, this.tableData1);
                Message({
                  type: 'success',
                  message: `${rowIndex}${btnIndex}`
                });
              }
            }
          ]
        }
      ],
      tableData1: [],
      tableData2: [
        {
          name: 'wa2',
          date: new Date().getTime()
        },
        {
          name: 'kesa1',
          date: new Date().getTime()
        }
      ]
    };
  },
  mounted() {
    this.ajaxGetTabelDataDemo();
  },
  methods: {
    getTableData,
    getTableData2,
    ajaxGetTabelDataDemo() {
      let data = [];
      for (let i = 0; i < 10; i++) {
        let now = new Date();
        data.push({
          date: now.getTime(),
          name: '|' + i,
          name2: '2|' + i
        });
      }
      this.tableData1 = data;
    },
    ajaxTest(params) {
      console.log(params);
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          if (Math.random() < 0.7) {
            resolve({ data: 'ok' });
          } else {
            reject({ data: 'err' });
          }
        }, 2000);
      });
    },
    spanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex % 2 === 0) {
          return {
            rowspan: 2,
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
    onPop() {
      this.$refs.checkPanel.show();
      // CFilterCheckPanel.show({
      //   data: this.headers1,
      //   ajaxFun: this.ajaxTest,
      //   successMsg: '',
      //   editOriginData: true,
      //   callback(data) {
      //     //console.log(data); // ['name', 'age' ...]
      //   }
      // });
    },
    onPop2() {
      CFilterCheckPanel.show({
        title: '',
        data: this.headers2,
        callback(data) {
          //console.log(data) // ['name', 'age' ...]
        }
      });
    },
    onTest(row, index) {
      console.log('test', row, index);
      Message({
        type: 'success',
        message: '' + index
      });
    },
    onCurrentChange(currentRow, oldCurrentRow) {
      console.log('current-change', currentRow, oldCurrentRow);
    },
    onHeaderClick(column, event) {
      console.log('header-click', column, event);
    },
    onRowClick(row, column, event) {
      console.log('row-click', row);
    },
    onRowDblClick(row, column, event) {
      console.log('row-dblclick', row);
    },
    onCellClick(row, column, cell, event) {
      console.log('cell-click', row);
    },
    onCellDblClick(row, column, cell, event) {
      console.log('cell-dblclick', row);
    }
  }
};
</script>
<style lang="less" scoped></style>
