import Vue from 'vue';
import file from './index.vue';
var CFCConstruct = Vue.extend(file);
var instance; ///

function close() {
  document.body.removeChild(instance.$el);
  instance = null;
}

var CFilterCheckPanel = {}; //

CFilterCheckPanel.show = function (options) {
  if (instance) {
    close();
  } //


  instance = new CFCConstruct({
    propsData: Object.assign({}, options, {
      onClosed: close
    })
  });
  instance.$mount();
  document.body.appendChild(instance.$el); //dom

  Vue.nextTick(function () {
    instance.show();
  });
};

export default CFilterCheckPanel;